body .is-layout-constrained .wp-block-blocks-gamestore-block-games-line {
	max-width: 100vw!important;
	margin-right: calc(var(--wp--style--root--padding-right) * -1)!important;
	margin-left: calc(var(--wp--style--root--padding-left) * -1)!important;
  }
  .wp-block-blocks-gamestore-block-games-line {
   
  
	.games-line-container {
	  width: 100%;
	  overflow: hidden;
	  position: relative;
  
	  > .swiper-wrapper {
		display: flex;
		transition: transform 3500ms linear;
		transition-timing-function: linear;
	  }
  
	  .game-item {
		width: 267px;
		height: 403px;
		position: relative;
		margin-left: 4px;
		margin-right: 4px;
  
		img {
		  object-fit: cover;
		  width: 100%;
		  height: 100%;
		  border-radius: 8px;
		}
	  }
	}
  }
  